
import React from 'react';
import millify from 'millify';
import {Typography, Row, Col, Statistic} from "antd";
import {Link} from 'react-router-dom';
import axios from 'axios'
const {Title} = Typography;
function Homepage(){
let [responseData, setResponseData] = React.useState('');
const fetchData = React.useCallback(() => {
  axios({
    "method": "GET",
    "url": "https://coinranking1.p.rapidapi.com/coins",
    "headers": {
     
      "x-rapidapi-host": "coinranking1.p.rapidapi.com",
      "x-rapidapi-key": "94b5abf932msha063fb09c4fa868p1828b2jsn645312cbca7b"
    }, "params": {
      referenceCurrencyUuid: 'yhjMzLPhuIDl',
      timePeriod: '24h',
      'tiers[0]': '1',
      orderBy: 'marketCap',
      orderDirection: 'desc',
      limit: '50',
      offset: '0'
    }
  })
  .then((response) => {
    setResponseData(response.data)
  })
  .catch((error) => {
    console.log(error)
  })
}, [])
React.useEffect(() => {
fetchData();
}, [setResponseData, responseData])

    return (
        
            <div className={"dark-global-crypto-stats"}>
                <Title level={2} className={"heading"}>Global Crypto Stats</Title>
                <main>
                  {responseData &&
                <Row>

                    <Col span={12}><Statistic title={"Total Cryptocurrencies"} value={responseData&&responseData.data.stats.total}/></Col>
                    <Col span={12}><Statistic title={"Total Exchanges"} value={responseData&&responseData.data.stats.totalExchanges}/></Col>
                    <Col span={12}><Statistic title={"Total Market Cap"} value={responseData&&responseData.data.stats.totalMarketCap}/></Col>
                    <Col span={12}><Statistic title={"Total 24h Volume"} value={responseData&&responseData.data.stats.total24hVolume}/></Col>
                    <Col span={12}><Statistic title={"Total Markets"} value={responseData&&responseData.data.stats.totalMarkets}/></Col>
                </Row>
}</main>
            </div>
           
       
    )
}

export default Homepage;